<script>
	export default {
		name: 'home',
		/* name */
		data() {
			return {
				msg: '' /* datareturn */
			}
		},
		mounted(){
			//
			var swipe={
				slidesPerView:"",
				spaceBetween: 10,
				prevButton: ".swiper-button-prev",
				nextButton: ".swiper-button-next",
				loop:true
			}
			
			function move(){
				//slidesPerView
				if ($(window).width()<=1920 && $(window).width()>1030) {
					swipe.slidesPerView=3;
				} else if ($(window).width()<1030 && $(window).width()>650) {
					swipe.slidesPerView=2;
				}else if ($(window).width()<650) {
					swipe.slidesPerView=1;
				}
			}
			move()
			
			$(window).resize(function(){
				move()
				var swiper = new Swiper(".homeBanner", swipe)
			})
			var swiper = new Swiper(".homeBanner",swipe)
			
			//
			function anima(num, po, time) {
				$('.pages'+num+' '+'.duihua_0').animate({opacity: po}, time, function() {
					$('.pages'+num+' '+'.duihua_1').animate({opacity: po}, time, function() {
						$('.pages'+num+' '+'.duihua_2').animate({opacity: po}, time, function() {
							$('.pages'+num+' '+'.duihua_3').animate({opacity: po}, time, function() {
								$('.pages'+num+' '+'.duihua_4').animate({opacity: po}, time)
							})
						})
					})
				})
			}
			$(".page_l").hover(function() {
				//console.log($(this).index())
				$(".duihua").css("opacity", 0);
				$(".pages").eq($(this).index()).css("opacity", 1);
				anima($(this).index(), 1, 200)
			}, function() {
				
			})
			$(".page_r").hover(function() {
				//console.log($(this).index())
				//arrindex=$(this).index()
				$(".duihua").css("opacity", 0);
				var arr2 = [5, 6, 7, 8, 9];
				var aaa=arr2[$(this).index()]
				//console.log(aaa)
				$(".pages").eq(aaa).css("opacity", 1);
				anima(aaa, 1, 200)
			}, function() {
				
			})
		}
	}
	
</script>


