	    position: relative;
	    overflow: hidden;
	    text-align: center;
	}
	.main img{
		width: 100%;
		height: 100%;
		transition: all 0.3s;
	}
	.main .motai{
		width: 100%;
		height: 100%;
		background: rgba(0,0,0,0.4);
		position: absolute;
	    top: 0;
	    left: 0;
	    -webkit-transition: all .3s;
    	-o-transition: all .3s;
	    transition: all .3s;
	}
	.main:hover .motai{
		opacity: 0;
	}
	.main:hover img{
		transform: scale(1.1,1.1); 
	}
	.main .motai span{
		position: absolute;
	    width: 100%;
	    bottom: 15%;
		display: block;
		font-size: 14px;
		color: #fff;
		opacity: 0.9;
	}
	.main .motai span:last-child{
		position: absolute;
	    width: 100%;
	    bottom: 4%;
		display: block;
		color: #fff;
	}
	
	.titlebox {
		width: 100%;
		position: relative;
	}
	.titlebox>img {
		width: 100%;
		height: 100%;
	}
	.titlebox .titleText {
		width: 100%;
		height: auto;
		position: absolute;
		bottom: 17%;
		left: 0;
		right: 0;
	}
	.titlebox .titleText h1 {
		text-align: center;
		color: #fff;
		font-size: 42px;
		font-weight: 100;
		margin-bottom: 10%;
	}
	.titlebox .titleText a {
		display: block;
		width: 100px;
		height: 25px;
		margin: 0 auto;
		padding: 10px 26px;
		border: 1px solid #fff;
		border-radius: 5px;
		line-height: 25px;
		text-align: center;
		font-size: 18px;
		font-weight: 300;
		color: #fff;
		transition: all .3s;
		-webkit-transition: all .3s;
		-o-transition: all .3s;
	}
	.titlebox .titleText a:hover {
		background-color: #fff;
		color: #000;
		cursor: pointer;
	}
	.titlebox .titleText a .sanjiao {
		display: inline-block;
		width: 0;
		height: 0;
		border-top: 10px solid transparent;
		border-left: 15px solid white;
		border-bottom: 10px solid transparent;
		vertical-align: middle;
		transition: all .3s;
		-webkit-transition: all .3s;
		-o-transition: all .3s;
	}
	.titlebox .titleText a:hover .sanjiao {
		border-top: 7.5px solid transparent;
		border-left: 15px solid #000;
		border-bottom: 7.5px solid transparent;
	}
	#sanjiao {
		display: block;
		width: 0;
		height: 0;
		border-top: 50px solid transparent;
		border-left: 100px solid red;
		border-bottom: 50px solid transparent;
	}
	
	
	.zhuiqiu_box {
		width: 100%;
		display: flex;
		justify-content: center;
		flex-wrap: wrap;
	}
	.zhuiqiu {
		width: 26%;
		margin: 20px;
		padding: 0 0 70px;
		text-align: center;
		-webkit-transition: all .3s;
		-o-transition: all .3s;
		transition: all .3s;
		-webkit-box-shadow: 10px 10px 40px rgba(0, 0, 0, .08), -10px -10px 40px rgba(0, 0, 0, .08);
		box-shadow: 10px 10px 40px rgba(0, 0, 0, .08), -10px -10px 40px rgba(0, 0, 0, .08);
	}
	.zhuiqiu:hover {
		-webkit-transform: translate3d(0, -5px, 0);
		transform: translate3d(0, -5px, 0);
		-webkit-box-shadow: 10px 40px 40px rgba(0, 0, 0, .09), 0 0 40px rgba(0, 0, 0, .09);
		box-shadow: 10px 40px 40px rgba(0, 0, 0, .09), 0 0 40px rgba(0, 0, 0, .09);
	}
	.zhuiqiu .hr {
		margin: 24px auto;
		border-top: 2px solid #ffe600;
		opacity: .6;
		width: 20%;
	}
	.zhuiqiu img {
		width: 190px;
		height: 190px;
		margin: 25px auto 0;
	}
	.zhuiqiu .zhuiqiu_text1 {
		margin-top: 36px;
		font-size: 18px;
		font-weight: 300;
		line-height: 25px;
		text-align: center;
	}
	.zhuiqiu .zhuiqiu_text2,
	.zhuiqiu .zhuiqiu_text3 {
		display: block;
		opacity: .4;
	}
	
	.vip_title{
		font-size: 24px;
	    font-weight: 300;
	    padding: 48px 0;
	    text-align: center;
	}
	
	.qingjing_box{
		width: 100%;
		margin: 111px 0 99px;
	}
	.qingjing_box .box {
		width: 100%;
		height: 700px;
		margin: 50px auto;
		display: flex;
		justify-content: space-between;
		position: relative;
	}
	
	.qingjing_box .box .box-l,.qingjing_box .box .box-r {
		position: absolute;
	}
	.qingjing_box .box .box-l {
		left: 20%;
		top: 7%;
	}
	.qingjing_box .box .box-r {
		right: 20%;
		top: 7%;
	}
	.qingjing_box .box .box-l .page_l {
		text-align: right;
	}
	.qingjing_box .box .box-r .page_r {
		text-align: left;
	}
	.qingjing_box .box .page_l,.page_r {
		position: relative;
		margin-bottom: 50px;
		color: #000;
		opacity: .4;
	}
	.qingjing_box .box .page_l:hover,.qingjing_box .box .page_r:hover{
	    opacity: 1;
	    cursor: pointer;
	}
	.qingjing_box .box .page_span1{
		display: block;
	    font-size: 14px;
	    margin-bottom: 6px;
	    font-weight: 300;
	}
	.qingjing_box .box .page_span2{
		font-size: 24px;
	    font-weight: 300;
	} 
	.qingjing_box .box .page_vipimg{
		position: absolute;
	    bottom: 3px;
	    right: -19px;
	    width: 30px;
	    height: 17px;
	}    
	.qingjing_box .box .box-c {
		width: 400px;
		height: 700px;
		margin: 0 auto;
		background: url(./../../static/laiye_img/phone_new.75a12d8.png) 0 0 no-repeat;
		background-size: contain;
		position: relative;
	}
	.qingjing_box .box .pages{
		width: 312px;
	    height: 400px;
	    margin: 125px 42px 0;
		position: absolute;
		top: 0;
		opacity: 0;
	} 
	.qingjing_box .box .p1,.p2{
		opacity: 0;
	}
	.qingjing_box .box .p2{
		max-width: 70%;
		float: right;
		line-height: 20px;
		padding: 8px 8px ;
		margin-bottom: 30px;
	    border-radius: 5px;
	    text-align: left;
	    background-color: #333;
	    color: #fff;
	    font-weight: 400;
	    font-size: 14px;
	    letter-spacing: 1px;
	    -webkit-transition: all .2s;
	    -o-transition: all .2s;
		transition: all .2s;
	}
	.qingjing_box .box .p1{
		max-width: 95%;
		float: left;
		text-align: left; 
	    margin-bottom: 30px;
	    -webkit-transition: all .2s;
	    -o-transition: all .2s;
		transition: all .2s;
	}
	.qingjing_box .box .p1 img{
		vertical-align: top;
	    width: 40px;
	    height: 40px;
	    display: inline-block;
	    margin-right: 10px;
	}
	.qingjing_box .box .p1 p{
		width: 225px;
		line-height: 20px;
		padding: 8px 8px;
	    border-radius: 5px;
	    background-color: #FEE600;
	    font-size: 14px;
	    font-weight: 400;
	    letter-spacing: 1px;
	    text-align: justify;
	    display: inline-block;
	} 
    
    .goutong_box{
    	width: 100%;
    	background-color: #f9f9f9;
    	padding: 97px 0 88px;
    	text-align: center;
    }
    .goutong_box>.title{
    	margin-top:0;
    }
	.goutong_box .erweima{
		width: 175px;
		height: 175px;
		margin-bottom: 12px;
	}
	.goutong_box .goutong_text1{
		font-size: 16px;
	    margin-bottom: 12.5px;
	    opacity: 0.6;
	}
	.goutong_box .goutong_text2{
		font-size: 16px;
		opacity: 0.6;
	}
	.goutong_box .goutong_text2 a{
		color: #0090ff;
	}

	.banner_box{
		width: 100%;
		height: 200px;
		position: relative;
	}
	.hide{
		width: 12%;
		height: 100%;
		background: #fff;
		position: absolute;
		z-index: 888;
	}
	.hide1{
		left: 0;
	}
	.hide2{
		right: 0;
	}
	.homeBanner{
		width: 100%;
		height: 230px;
		margin: 0 auto;
		box-sizing: border-box;
		padding: 0 12%;
	}
	.homeBanner img{
		width: 100%;
		height: 100%;
	}
	.homeBanner .swiper-wrapper .swiper-slide{
		width: 30%;
		height: 100%;
		margin: 0;
	}
	.homeBanner .swiper-wrapper .main:hover .motai{
		opacity: 1;
		background: rgba(0,0,0,0);
	}
	.homeBanner .swiper-wrapper .main:hover img{
		transform: none; 
	}
	.swiper-button-prev,.swiper-button-next {
	    background-image: none;
	    width: 56px;
	    height: 56px;
	    z-index: 999 !important;
	}
	.swiper-button-prev{
		left: 5%;
	}
	.swiper-button-next{
		right: 5%;
	}
	
	.foot_img_box{
		width: 100%;
		height: auto;
		margin-top: 100px;
		position: relative;
		padding: 0;
	}
	.foot_img_box img{
		width: 100%;
		height: 100%;
	}
	.foot_text_box{
		width: 100%;
		height: 100%;
		color: #fff;
		position: absolute;
		top: 0;
		opacity: 0.9;
	} 
	.foot_text_text1{
		width: 90%;
		margin: 20% auto 0px;
		font-size: 36px;
	    line-height: 72px;
	}
	.foot_text_text2{
		width: 90%;
		margin: 5% auto 0px;
		font-size: 32px;
	    margin-top: 40px;
	    text-align: right;
	}
	
	@media screen and (max-width:1200px) {
		.foot_text_text1{margin: 150px auto 0px;}
	}
	@media screen and (max-width:1060px) {
		.qingjing_box {display: none;}
		.foot_text_text1{margin: 7% auto 0px;font-size: 28px;}
		.foot_text_text2{font-size: 28px;}
	}
	@media screen and (max-width:960px){
		.main{width: 20%;}
	}
	@media screen and (max-width:750px){
		.main{width: 27%;}
		.foot_text_text1{margin: 5% auto 0px;font-size: 26px;line-height: 40px;}
		.foot_text_text2{font-size: 26px;margin-top: 30px;}
		.zhuiqiu {width: 40%;}
		.title{margin: 60px 0 40px;}
		.title .title_text1 {font-size: 38px;}
		.goutong_box{padding: 50px 0 40px;margin-top: 30px;}
	}
	@media screen and (max-width:560px){
		.main{width: 40%;}
		.titlebox .titleText h1 {font-size: 32px;margin-bottom: 5%;}
		.foot_text_text1{margin: 3% auto 0px;font-size: 22px;line-height: 34px;}
		.foot_text_text2{font-size: 22px;margin-top: 20px;}
		.zhuiqiu {width: 80%;}
		.title{margin: 60px 0 40px;}
		.title .title_text1 {font-size: 38px;}
		.goutong_box{padding: 50px 0 40px;margin-top: 30px;}
	}
	@media screen and (max-width:430px) {
		.titlebox .titleText{top: 30px;bottom: 0;}
		.titlebox .titleText h1 {font-size: 26px;margin-bottom: 5%;}
		.titlebox .titleText a{padding: 7px 20px;font-size: 16px;}
		.foot_text_text1{margin: 3% auto 0px;font-size: 20px;line-height: 30px;}
		.foot_text_text2{font-size: 20px;margin-top: 15px;}
	}
	@media screen and (max-width:375px) {
		.title{margin: 50px 0 35px;}
		.title .title_text1 {font-size: 32px;}
		.title .title_text2 {font-size: 14px;}
	}
</style>


