import Vue from 'vue' //vue
import Router from 'vue-router' /* vue,router*/
import home from '@/components/home' /*hello.vue,hello,@../*/
import serve from '@/components/serve'
import cooperation from '@/components/cooperation'



Vue.use(Router) /**/

export default new Router({
	routes: [
	{ /* ,/hello*/
		path: '/',
		name: 'home',
		/*name,*/
		component: home /*hello*/
	},
	{
		path:"/home",
		name: 'home',
		component: home
	},
	{
		path:"/serve",
		name: 'serve',
		component: serve
	},
	{
		path:"/cooperation",
		name: 'cooperation',
		component: cooperation
	}]
})


